@font-face {
	font-family: "Amaranth";
	font-display: swap;
	src : url('./fonts/Amaranth Regular 400.woff2') format('woff2'),
		url('./fonts/Amaranth Regular 400.woff') format('woff');
}

body {
	font-family: 'Amaranth';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.center {
	display: block !important;
	margin: auto !important;
}

.linkNone {
	text-decoration: none;
	color: black;
}

#sujet table, #sujetPDF table{
	border-collapse: collapse;
}

#sujet td {
	border: 1px solid black;
	padding: 3px 10px;
	text-align: center;
}

#sujetPDF {
	position: fixed; 
	font-size: 0.230rem; 
	line-height: 0.95; 
	letter-spacing: 0.01071em; 
	width: 10%;
}

#sujetPDF td {
	border: 0.01em solid #a3a3a3;
	padding: 3px 10px;
	text-align: center;
}

#sujetPDF p:first-child {
	margin: 0;
	text-align: center;
}